<template>
	<section class="add-sub-account" v-loading="listLoading">
		<el-form :model="ruleForm" label-width="80px" :rules="rules" ref="ruleForm">
			<el-form-item label="" label-width="100px" prop="account">
				<el-input :maxlength="20" v-model="ruleForm.account" placeholder="" class="wid280" @blur="findName"></el-input>
			</el-form-item>
			<el-form-item label=""  label-width="100px" prop="password" class="passhezi">
				<el-input :maxlength="20" :type="types" v-model="ruleForm.password" placeholder=""  class="wid280" auto-complete="off"></el-input>
        <span class="iconfont" :class="iconCls ? 'icon-yanjing' : 'icon-biyan1'" @click="switchs"></span>
			</el-form-item>
			<el-form-item label=""  label-width="100px" prop="name">
				<el-input :maxlength="30" v-model="ruleForm.name" placeholder="" class="wid280"></el-input>
			</el-form-item>
			<el-form-item label="" label-width="100px" prop="gender" class="requireHezi">
          <span class="require" style='left:-50px;'>*</span>
			    <el-radio-group v-model="ruleForm.gender">
			      <el-radio :label="1"></el-radio>
			      <el-radio :label="2"></el-radio>
			    </el-radio-group>
			</el-form-item>
      <el-form-item label="" prop="roleId" label-width="100px">
        <el-select v-model="ruleForm.roleId" placeholder="" @change="getRoleId">
          <el-option
            v-for="item in ruleForm.role"
            :key="item.id"
            :label="item.roleName"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
			<el-form-item label="" label-width="100px" prop="mobile">
				<el-input :maxlength="11" v-model="ruleForm.mobile" placeholder="" class="wid280"></el-input>
			</el-form-item>
			<el-form-item label="" label-width="100px" prop="identityNumber">
				<el-input :maxlength="18" v-model="ruleForm.identityNumber" placeholder="" class="wid280"></el-input>
			</el-form-item> 
			<el-form-item label="" label-width="100px">
				<el-button class="wid100" type="primary" @click="submitForm('ruleForm')"></el-button>
			</el-form-item>
			
		</el-form>
	</section>
	
</template>

<script>
import {operatorGet,operatorUpdate,operatorSave,operatorCheckaccount,roleUsedlist} from '@/api/shopApi';
import md5 from 'js-md5';
import crypto from 'crypto'
  export default {
    data() {
      var validatePhone = (rule, value, callback) => {
          if (value === '') {
            callback(new Error(''));
          } else {
            var first=value.slice(0,1);
            if ((value.length!=11)||(first!=1)) {
                callback(new Error(''));
            }else{
              callback();
            }
          }
      };
      var validatePassword= (rule, value, callback) => {
          var reg = new RegExp("[\\u4E00-\\u9FFF]+","g");
          if (reg.test(value)) {
            callback(new Error(''));
          } else {
            callback();
          }
      };
      var validateNumber1= (rule, value, callback) => {
          var reg =/^\d+$/g;
          if (!value.match(reg)) {
            callback(new Error(''));
          } else {
            callback();
          }
      };
      var validateNumber2= (rule, value, callback) => {
          var reg =/(^\d{0,18}$)|(^\d{17}(\d|X|x)$)/g;
          if (!value.match(reg)) {
            callback(new Error(''));
          } else {
            callback();
          }
      };
      return {
        iconCls:false,
        types:"password",
        roleList:[],
        listLoading:false,
        isAdd:1,
        csname:'',
        flag:0,
        ruleForm: {
          storeOperatorId:'',
          account: '',
          password: '',
          againpass: '',
          name: '',
          gender: 1,
          role:'',
          roleId:'',
          newRoleId:'',
          mobile: '',
          identityNumber: ''
        },

        rules: {
          account: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 20, message: ' 1  20 ', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '', trigger: 'blur' },
            { min: 6, max: 20, message: '6,20', trigger: 'blur' },
            {  validator: validatePassword, trigger: 'blur'},
            {  validator: validatePassword, trigger: 'change'}
          ],
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min:2, max: 30, message: '', trigger: 'blur' }
          ],
          roleId: [
            {  required: true, message: '', trigger: 'change' }
          ],
          mobile: [
            { required: true,validator: validatePhone,trigger: 'blur' },
            { validator:validateNumber1,trigger:'blur'},
            { validator:validateNumber1,trigger: 'change'}
          ],
          identityNumber: [
            { required: true, message: '', trigger: 'blur' },
            { min:18, max: 18, message: '', trigger: 'blur' },
            { validator:validateNumber2,trigger:'blur'},
            { validator:validateNumber2,trigger:'change'}
          ]
        }
      };
    },
    mounted:function(){
       //
      let para = {
        storeId:config.storeId
      };
      roleUsedlist(para).then((res) => {
        if(res.data.code==0){
          this.roleList=res.data.data;
          this.ruleForm.role=res.data.data;
          

        }
        
      });

      /**/
      var id=this.$route.query.id;
      if(id){
        //
        this.isAdd=2;
        this.dataFetch(id);
      }
    },
    methods: {
      //
      switchs(){
        if(this.types=="password"){
          this.types='text';
          this.iconCls=true;
        }else{
          this.types='password';
           this.iconCls=false;
        }
        
      },
      /**/
      findName(val){
        if(!(this.ruleForm.account)||(this.ruleForm.account==this.csname)){
          return false;
        }
        operatorCheckaccount({"account":this.ruleForm.account,storeId: config.storeId}).then((res) => {
            if(res.data.code==1){
              this.$message.error('');
            }
        });
      },
      getRoleId(){
        this.flag++;
      },
      /**/
      dataFetch(id){ 
        let para = {
          storeId: config.storeId,
          storeOperatorId:id
        };
        this.listLoading = true;
        operatorGet(para).then((res) => {
          if (res.data.code==0) {
            this.ruleForm= res.data.data;
            this.ruleForm.againpass=this.ruleForm.password;
            this.ruleForm.role=this.roleList;

            this.ruleForm.password="********************";
            if(res.data.data.roleName){
              this.ruleForm.newRoleId=this.ruleForm.roleId;
              this.ruleForm.roleId=res.data.data.roleName;

            }
            this.csname=this.ruleForm.account;
          }else{
            this.$message.error(res.data.message);
          }
          this.listLoading = false;
        }).catch((res)=> {
          this.listLoading = false;
          this.$message.error('');
        });

      },
      //
      sucFun(res){
        var that=this;
        this.listLoading = false;

        if(res.data.code==0){
          this.$message({message: '',type: 'success',
            onClose:function(){
              that.$router.push({ path: '/store/bypass-management/account-list' });
            }
          });
        }else{
          this.$message.error(res.data.message);
        }
      },

      submitForm(formName) {
         
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.listLoading = true;
    
            var para = new URLSearchParams();
            para.append('storeId',config.storeId);
            para.append('account',this.ruleForm.account);
            
            para.append('name',this.ruleForm.name);
            para.append('gender',this.ruleForm.gender);
           
            para.append('mobile',this.ruleForm.mobile);
            para.append('identityNumber',this.ruleForm.identityNumber);
            
            if(this.isAdd==2){//2
              if(this.ruleForm.password!="********************"){
                para.append('password',md5(this.ruleForm.password));
              }
              if(this.flag==1){
                para.append('roleId',this.ruleForm.newRoleId);
              }else{
                para.append('roleId',this.ruleForm.roleId);
              }
              para.append('storeOperatorId',this.ruleForm.storeOperatorId);
              operatorUpdate(para).then((res)=> { 
                this.sucFun(res);  
              }).catch((res)=> {
                this.listLoading = false;
                this.$message.error('');
              });
            }else{
              para.append('roleId',this.ruleForm.roleId);
              para.append('password',md5(this.ruleForm.password));
              para.append('storeId',config.storeId);
              operatorSave(para).then((res)=> {
                this.sucFun(res);
              }).catch((res)=> {
                this.listLoading = false;
                this.$message.error('');
              });
            }
            
          } else {
            return false;
          }
        });
      }
    }
  }
</script>


